.inlineblock(){
    display: inline-block;
    *display:inline;
    *zoom:1;
}
.ellipsis(){
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    width:99%;
}

.radius(@r:5px){
    -webkit-border-radius:@r;
    -moz-border-radius:@r;
    border-radius:@r;
    // behavior: url(/htc/PIE.htc);
}

.box-sizing(){
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}
